import { Config, DAppProvider, Mainnet } from '@usedapp/core';
import React from 'react';
import { HashRouter } from 'react-router-dom';
import Routes from './routes';

const config: Config = {
  readOnlyChainId: Mainnet.chainId,
  readOnlyUrls: {},
};
function App() {
  return (
    <DAppProvider config={config}>
      <HashRouter>
        <Routes />
      </HashRouter>
    </DAppProvider>
  );
}

export default App;
