import React from 'react';
import { message } from 'antd';
import useContract from '@/hooks/chain/useContract';

function Index(porps: any) {
  const { claimMintReward } = useContract();

  const handleClaimMintReward = () => {
    claimMintReward()
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        message.error(err.error?.message || 'Request failed');
      });
  };

  return (
    <div className="w-full mt-5">
      <button
        onClick={handleClaimMintReward}
        className="border bg-[#525252] text-gray-400 border-[#159374] rounded-full  py-1  text-center w-1/2"
      >
        Claim in {porps.term || '-'} days
      </button>
    </div>
  );
}

export default Index;
